import React from "react"
import * as THREE from "three"
import * as OrbitControls from "three-orbitcontrols"
import delay from "../../../utils/delay"
// const THREE = require("three")
// const OrbitControls = require("three-orbitcontrols")
// 

class Introduce extends React.Component {
  constructor(props) {
    super(props)
    this.scene = null
    this.renderer = null
    this.camera = null

    this.cubeMesh = null
    this.sphereMesh = null
    this.cylindeMesh = null

    this.T0 = 0
    this.T1 = 0
  }

  componentDidMount() {
    this.init()
    // setInterval(() => {
    //   this.renderGL()
    // }, 20)
  }

  async init() {
    this.scene = new THREE.Scene()
    this.initMesh()

    //
    let axisHelper = new THREE.AxisHelper(250)
    this.scene.add(axisHelper)

    //
    let point = new THREE.PointLight(0xffffff)
    point.position.set(400, 200, 300) //
    this.scene.add(point) //
    //
    let ambient = new THREE.AmbientLight(0x444444)
    this.scene.add(ambient)

    await delay(10)
    let width = this.refs.three.clientWidth //window.innerWidth //
    let height = this.refs.three.clientHeight //window.innerHeight //
    let k = width / height //
    let s = 200 //
    // CAMERA
    this.camera = new THREE.OrthographicCamera(-s * k, s * k, s, -s, 1, 1000)
    this.camera.position.set(200, 100, 100) //
    this.camera.lookAt(this.scene.position) //()
    // RENDERER
    this.renderer = new THREE.WebGLRenderer()
    this.renderer.setSize(width, height) //
    this.renderer.setClearColor(0x00000, 1) //
    this.refs.three.appendChild(this.renderer.domElement) //bodycanvas
    //   
    this.T0 = new Date()
    this.renderGL()
    new OrbitControls(this.camera, this.renderer.domElement)

    // let controls = new OrbitControls(this.camera, this.renderer.domElement)
    // controls.addEventListener("change", this.renderGL.bind(this))
  }

  initMesh() {
    let sphereGeo = new THREE.SphereGeometry(50, 10, 10) //
    let cubeGeo = new THREE.BoxGeometry(200, 100, 50) //Geometry
    let cylindeGeo = new THREE.CylinderGeometry(50, 30, 90, 100, 100)
    let materialLambert = new THREE.MeshLambertMaterial({
      color: 0x0000ff,
      opacity: 0.7,
      transparent: true,
    })
    let materialBasic = new THREE.MeshBasicMaterial({
      color: 0xffff00,
      wireframe: true,
    })
    let materialPhong = new THREE.MeshPhongMaterial({
      color: 0x0000ff,
      specular: 0x4488ee,
      shininess: 12,
    })
    this.cubeMesh = new THREE.Mesh(cubeGeo, materialLambert)
    this.sphereMesh = new THREE.Mesh(sphereGeo, materialBasic)
    this.cylindeMesh = new THREE.Mesh(cylindeGeo, materialPhong)
    this.cubeMesh.position.set(0, 0, 0)
    this.sphereMesh.position.set(0, 100, 0) //
    this.cylindeMesh.position.set(200, 0, 0)
    this.scene.add(this.cubeMesh)
    this.scene.add(this.sphereMesh)
    this.scene.add(this.cylindeMesh)
  }

  renderGL() {
    // 
    this.T1 = new Date()
    let t = this.T1 - this.T0
    this.T0 = this.T1
    //console.log(t) // t = 16.7ms 16~17
    this.renderer.render(this.scene, this.camera) //
    this.cubeMesh.rotateY(0.001 * t) //y0.01 2PI 6.28 0.5
    this.sphereMesh.rotateY(0.002 * t)
    requestAnimationFrame(this.renderGL.bind(this))
  }

  render() {
    return (
      <div
        ref="three"
        id="three"
        style={{ width: "100%", height: "100%" }}
      ></div>
    )
  }
}

export default Introduce
